<template>
  <div id="app">
    <div class="container">
      <Head @addList="addList"/>
      <BodyCon :mylist='list'/>
      <Footer :mylist='list' @checkall="checkall" @clearall="clearall"/>
    </div>
  </div>
</template>

<script>
// import pubsub from 'pubsub'
import Head from './components/Head.vue'
import Footer from './components/Footer.vue'
import BodyCon from './components/BodyCon.vue'
export default {
    name: 'App',
    data(){
      return {
        list:JSON.parse(localStorage.getItem('list'))||[ ]
      }
    },
    methods:{
        addList(newtodo){
          this.list.unshift(newtodo);
     },
     reversestatus(id){
      this.list.forEach((item)=>{
        if(item.id===id){
          item.done=!item.done;
        }
      })
     },
     deleteitem(id){
      this.list.forEach((item)=>{
        if(item.id===id){
          this.list.pop(item)
        }
      });
     },
     update(id,name){
      this.list.forEach((item)=>{
        if(item.id===id){
         item.name=name;
        }
      });
     },
     checkall(done){
      this.list.forEach((item)=>{
        item.done=done;
      })
     },
     clearall(){
        this.list=this.list.filter((item)=>{
        return !item.done;
      })
     }
     
    },
    watch:{
      list:{
        deep:true,
        handler(value){
            localStorage.setItem('list',JSON.stringify(value));
        }
      }
    },
    components: {
      Head,
      BodyCon,
      Footer,
  },
  mounted(){
    this.$bus.$on('reversestatus',this.reversestatus);
    // this.pubid=pubsub.subcribe('deleteitem',this.deleteitem)
    this.$bus.$on('deleteitem',this.deleteitem);
    this.$bus.$on('update',this.update);
  },
  beforeDestroy(){
    this.$bus.$off(['reversestatus','deleteitem','update']);
  }

}
</script>
<style>
  #app {
    display: flex;
    justify-content: center;
    width: 90%;
    margin:auto;
    align-items: center;
    border: 5px solid rgba(0, 250, 154, 0.507);
    border-radius: 20px 20px;
    background: rgba(172, 255, 47, 0.27);
  }
  .container{
    margin:0 auto;
    width: 80%;
    border-radius: 20px 20px;
  }
</style>
