    <div class="box">
        <input type="text" placeholder="请输入代办事项" v-model="notdone" @keyup.enter="tip">
    </div>
</template>

<script>
export default {
    // eslint-disable-next-line vue/multi-word-component-names
    name:'Head',
    // props:['addList'],
    data(){
        return {notdone:''}
    },
    // props:['addlist'],
    methods:{
        tip(){
            if(!this.notdone){
                alert('你还未填写任何内容哦！');
                return;
            }
            const newlist={id:Math.ceil(Math.random()*100000000),name:`${this.notdone}`,done:false};
            console.log(newlist);
            this.$emit('addList',newlist);
            this.notdone=''
            // this.addlist(newlist);
        }
    }
}
</script>

