<template>
    <div class="box" v-show="allitem">
        <li>
            <input type="checkbox" v-model="alldone">
            <span class="done">已完成{{donecount}}/</span>
            <span class="all">全部{{allitem}}</span>
            <button @click="clearAll">清除</button>
        </li>
    </div>
</template>

<script>

export default {
    // eslint-disable-next-line vue/multi-word-component-names
    name:'Footer',
    props:['mylist'],
    computed:{
        allitem(){
            return this.mylist.length;
        },
        donecount(){
            ///常规做法
            // let num=0;
            // this.mylist.forEach(element => {
            //     if(element.done) num ++;
            // });
            // return num;
            //骚操做
            return this.mylist.reduce((pre,cur)=>pre+(cur.done ? 1 : 0),0);

        },
        alldone:{
            get(){
                 return this.allitem===this.donecount && this.allitem>0;
            },
            set(value){
                this.$emit('checkall',value);
            }
        }
    },
    methods:{
        clearAll(){
            this.$emit('clearall');
        }
    }
}
</script>
