<template>
   <div class="box " >
    <transition 
     appear
    name="animate__animated animate__bounce" 
    enter-active-class="animate__bounceIn" 
    leave-active-class="animate__flipOutX"
    >
        <li>
          <div class="inputbox">
            <input class="check" type="checkbox" 
            :checked="mylist.done" 
            @change="alterstatus(mylist.id)">
            <transition appear name="animate__animated animate__bounce"
            enter-active-class="animate__zoomIn"
            >
                <input class="edit" type="text" v-show="mylist.edit" ref="edit"
                @keydown.enter="finish(mylist,$event)"
                @blur="finish(mylist,$event)" 
                :value="mylist.name">
            </transition>
            
          </div>
          <span v-show="!mylist.edit">{{mylist.name}}</span>
          <div class="btn-con">
              <button class="btnchange" @click="edit(mylist)" v-show="!mylist.edit">更改</button>
              <button class="btndelete" @click="deletelist(mylist.id)">删除</button>
          </div>
        </li>
    </transition>
        
   </div>
</template>

<script>
// import pubsub from 'pubsub'
import 'animate.css'
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'Item',
  props:['mylist'],
  methods:{
    // eslint-disable-next-line vue/no-dupe-keys
    alterstatus(id){
      this.$bus.$emit('reversestatus',id);
    },
    deletelist(id){
      // pubsub.publish('deleteitem',id)
      this.$bus.$emit('deleteitem',id);
    },
    edit(mylist){
      // eslint-disable-next-line no-prototype-builtins
      if(mylist.hasOwnProperty('edit')){
        mylist.edit=true;
      }
      this.$set(mylist,'edit',true)
      this.$nextTick(function(){
        this.$refs.edit.focus()
      })
    },
    finish(mylist,e){
      mylist.edit=false;
      if(e.target.value===''){
        confirm('输入内容再更改哦！');
        return;
      }
      console.log(e.target.value);
      this.$bus.$emit('update',mylist.id,e.target.value);
    }
  }
}
</script>
